import React, {useState} from 'react';
import marked from 'marked';
import './previsual.css';

marked.setOptions({
  breaks: true,  
});

function Previewer() {
  const [value, setValue] = useState('');

  const onChange = (event) => {
    setValue(event.target.value);
  };

  return (
    <>
    <header className='titulo'>Previsualizador Markdown</header>
    <div className='contenedor'>
      <textarea className='textarea' value={value} onChange={onChange} />
      <div className='textarea' value={value} dangerouslySetInnerHTML={{ __html: marked(value) }} readOnly ></div>
    </div>
    </>
  );
}

export default Previewer;